import React from 'react';
import { createMuiTheme, ThemeProvider } from "@material-ui/core/styles";
import * as colors from "@material-ui/core/colors";
import CssBaseline from "@material-ui/core/CssBaseline";
import 'fontsource-roboto';
import GlobalMenu from './components/GlobalMenu';
import WordPage from './pages/WordPage';
import WordTransPage from './pages/WordTransPage';
import WordGrammerPage from './pages/WordGrammerPage';
import GrammerPage from './pages/GrammerPage';
import EnglishData from './context/EnglishData';
import GrammerData from './context/GrammerData';

interface AppProps {
}

interface AppState {
  currentPage: pageNames,
  isLoaded: boolean,
}

class App extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);
    let sPage:pageNames = 'WordPage';
    switch (localStorage.getItem('currentPage')) {
      case 'WordPage':  sPage = 'WordPage';
            break;
      case 'GrammerPage': sPage = 'GrammerPage';
            break;
      case 'WordGrammerPage': sPage = 'WordGrammerPage';
            break;
      case 'WordTransPage': sPage = 'WordTransPage';
            break;
      default: sPage = 'WordPage';
    }
    this.state = {
      currentPage: sPage,
      isLoaded: false,
    }
  }

  async componentDidMount() {
    await EnglishData.setWordData();
    await GrammerData.setGrammerData()
    await this.setState({isLoaded: true});
  }

  render () {
    if (this.state.isLoaded === false){
      return null;
    }

    const theme = createMuiTheme({
      palette: {
        primary: {
          main: colors.blue[800],
        },
        type: 'dark',
      },
    });

    const changePage = async (val: pageNames) => {
      this.setState({
        currentPage: val
      })
      if (val === 'WordPage') {
        await EnglishData.setWordData();
      } else if (val === 'WordTransPage') {
        await GrammerData.setGrammerData();
      }
    }

    return (
      <ThemeProvider theme={theme}>
        <CssBaseline />
        {
          this.state.currentPage === 'WordPage'
          &&
          <WordPage />
        }
        {
          this.state.currentPage === 'WordTransPage'
          &&
          <WordTransPage />
        }
        {
          this.state.currentPage === 'WordGrammerPage'
          &&
          <WordGrammerPage />
        }
        {
          this.state.currentPage === 'GrammerPage'
          &&
          <GrammerPage />
        }
        <GlobalMenu
            currentPage={this.state.currentPage}
            changeCurrentPage= {(val) => changePage(val)}
        />
      </ThemeProvider>
    );
  }
}

export default App;